.si-rounded {
  border-radius: 50%; }

/* Social Icons - Borderless
-----------------------------------------------------------------*/
.si-borderless {
  border-color: transparent !important; }

/* Social Icons - Dark
-----------------------------------------------------------------*/
.si-dark {
  background-color: #444;
  color: #FFF !important;
  border-color: transparent; }

/* Social Icons - Light
-----------------------------------------------------------------*/
.si-light {
  background-color: rgba(0, 0, 0, 0.05);
  color: #666 !important;
  border-color: transparent; }

/* Social Icons - Colored
-----------------------------------------------------------------*/
.si-colored {
  border-color: transparent !important; }
  .si-colored i {
    color: #FFF; }

/* Social Icons - Large
-----------------------------------------------------------------*/
.social-icon.si-large {
  margin: 0 10px 10px 0;
  width: 56px;
  height: 56px;
  font-size: 30px;
  line-height: 54px !important; }

/* Social Icons - Small
-----------------------------------------------------------------*/
.social-icon.si-small {
  width: 32px;
  height: 32px;
  font-size: 14px;
  line-height: 30px !important; }

.social-icon i {
  display: block;
  position: relative; }

body:not(.device-touch) .social-icon i {
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.social-icon i:last-child {
  color: #FFF !important; }

.social-icon:hover {
  color: #555;
  border-color: transparent; }
  .social-icon:hover i:first-child {
    margin-top: -38px; }

.social-icon.si-large:hover i:first-child {
  margin-top: -54px; }

.social-icon.si-small:hover i:first-child {
  margin-top: -30px; }

.si-colored.social-icon {
  border-color: transparent; }

/* Social Icons - Sticky
-----------------------------------------------------------------*/
.si-sticky {
  position: fixed;
  top: 50%;
  left: 5px;
  width: 36px;
  z-index: 100; }

/* Social Icons - Sticky Right
-----------------------------------------------------------------*/
.si-sticky.si-sticky-right {
  left: auto;
  right: 8px; }

/* Social Icons - Share
-----------------------------------------------------------------*/
.si-share {
  position: relative;
  padding-left: 5px; }
  .si-share span {
    line-height: 36px;
    font-weight: 500;
    font-size: 13px; }
  .si-share div {
    float: left; }
  .si-share .social-icon {
    margin: -1px 0;
    border-radius: 0;
    font-size: 15px; }

